<template>
  <div>
    <div class="MemberInfoG">
      <span class="arrow" @click="ToLeft">&lt;</span>
      <div class="yearListBox">
        <ul class="yearList" ref="ulYear">
          <li :class="{active:index===current}" v-for="(value,index) in years" :key="value" @click="songetYear(value,index)">
            {{value}}
          </li>
        </ul>
      </div>
      <span class="arrow" @click="ToRight">&gt;</span>
    </div>
    <div class="Members">
      <span class="arrow" @click="Left" v-show="change">&lt;</span>
      <ul class="memList" ref="ulMem">
        <li class="mmm" v-for="(value ,index) in currentParentMen" :key="index"><img v-lazy="value.img" alt="">
            <h4>{{value.name}}</h4>
            <p>{{value.quote}}</p>
          </li>
        </ul>

      <span class="arrow" @click="Right" v-show="change">&gt;</span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'MemberInfoGrade',
  data: function () {
    return {
      years: [2019, 2018, 2017, 2016, 2015, 2014, 2013, 2012, 2011],
      now: 0,
      current: 0,
      arrowS: true,
      currentParentMen: [],
      totalPage: 1,
      currentPage: 1,
      pageSize: 4,
      change: false
    }
  },
  props: {
    parentMen: {
      type: Array,
      default: () => [],
      required: true
    }
  },
  watch: {
    parentMen (n, o) {
      this.currentPage = 1
      this.totalPage = Math.ceil(this.parentMen.length / this.pageSize)
      this.totalPage = this.totalPage === 0 ? 1 : this.totalPage
      this.change = this.totalPage > 1
      this.setCurrentPageData()
    }
  },
  methods: {
    songetYear: function (value, index) {
      this.$emit('parentYear', value)
      this.current = index
      // this.$refs.ulMem.style.width = `${this.props.parentCount}px`
    },
    ToRight: function () {
      if (this.now < 2) {
        this.now++
        const ele = document.getElementsByClassName('yearListBox')[0]
        const width = window.getComputedStyle(ele).width
        this.$refs.ulYear.style.marginLeft = `${this.now * -parseFloat(width)}px`
      }
    },
    setCurrentPageData: function () {
      const begin = (this.currentPage - 1) * this.pageSize
      const end = this.currentPage * this.pageSize
      this.currentParentMen = this.parentMen.slice(begin, end)
    },
    ToLeft: function () {
      if (this.now > 0) {
        this.now--
        const ele = document.getElementsByClassName('yearListBox')[0]
        const width = window.getComputedStyle(ele).width
        this.$refs.ulYear.style.marginLeft = `${this.now * -parseFloat(width)}px`
      }
    },
    Right: function () {
      if (this.currentPage === this.totalPage) return
      this.currentPage++
      this.setCurrentPageData()
    },
    Left: function () {
      if (this.currentPage === 1) return
      this.currentPage--
      this.setCurrentPageData()
    }
  }
}
</script>

