<template>
    <div class="Nav">
      <div class="NavLeft">
        <img src="../../assets/images/logfor2.c78f4a2e.png"  alt="">
      </div>
      <ul class="NavRight">
        <li v-for="(value,index) in list" :key="index" @click="navActive(index) " >{{value}}</li>
      </ul>
    </div>
</template>

<script>
export default {
  name: 'Nav',
  data: function () {
    return {
      list: ['部门', '成员', '历史', '作品', '留言', '加入'],
      current: -1,
      comList: ['Department', 'Member', 'History', 'Work', 'Comments']
    }
  },
  methods: {
    navActive: function (index) {
      if (index !== 5) {
        this.current = index
        document.getElementById(this.comList[index]).scrollIntoView({ behavior: 'smooth' })
      }
      if (index === 5) {
        this.$router.push('/login')
      }
    }
  }
}
</script>

